export class Company {
  id: any;
  name: String; 
  isActive: any;
  groups: any
  creator : any;
  modifier : any;
}

export class User {
  firstName : String;
  lastName : String;
  emailAddress : String;
  isActive : any;
  isCustomerAdmin : any;
  isSuccessHackerAdmin : any;
  groups : any = [];
  company : any = "";
  creator : any;
  modifier : any;
}

export class UserSearch {
  page: any;
  limit: any;
  company: any;
  active: any;
  group: any;
  search: any;
  sort: any;
  sortOrder: String = 'ASC';
}


